<template>
    <header class="header">
        <a href="" class="logo">{{ logoText }}</a>
        <ul>
            <li v-for="item, index in links" :key="index">
                <!-- <a href="" @click.prevent="send(item)">{{ item }}</a> -->
                <a href="" @click.prevent="$emit('linkClick', item)">{{ item }}</a>
            </li>
            <!-- <li>button</li>
            <li>button</li> -->
        </ul>
    </header>
</template>

<script>
    export default {
        // props: ['logoText']
        props: {
            logoText: {
                type: String,
                default: 'Vue'
            },
            links: Array
        },
        methods: {
            send(text){
               this.$emit('linkClick', text) 
            }
        }
    }
</script>

<style>
.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #000;
    color: #fff;
    font-size: 30px;
    padding: 30px;
}

ul {
    display: flex;
    gap: 30px;
}
</style>